@import url('https://fonts.googleapis.com/css2?family=Inter&display=swap');
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    font-family: 'Inter', sans-serif;

}

.main {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #313030;

}

.container {

    width: 70%;
    height: 80%;
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-template-rows: auto;
    box-shadow: rgba(0, 0, 0, 0.56) 0px 72px 90px 20px;
    background-image: url(https://images.pexels.com/photos/110874/pexels-photo-110874.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1);
    background-size: cover;
}

.container .left {
    color:white;



}

.container .left .my-name
{
    position: relative;
    top:5rem;
    left: 11rem;
}

.container .left .temp {
    position: relative;
    top: 30rem;
    left: 10rem;
    display: flex;



}

.container .left .temp .temp-value{
    font-size: 7rem;

}


.container .left .temp .city{
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin:0 1.5rem 0 1rem;
}

.container .left .temp .city .city-name{
    font-size: 3.5rem;
}

.container .left .temp .city .time{
    font-size: 1rem;
}


.container .left .temp .icon{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
 

}

.container .left .temp .icon img{
    height:4rem;
    width: 4rem;
}



/*right search bar part*/
.container .right {
    backdrop-filter: blur(10px);
    background-color: rgba(0, 0, 0, 0.6);
    color:white;
  

   


}

.container .right .search-part
{
    display: flex;
    justify-content: space-between;
    padding:0 0 0 2rem;
    
}


.container .right .search-part input{
    padding:1rem 0;
    outline: none;
    background: transparent;
    border: none;
    border-bottom: 1px solid rgb(30, 50, 15);
    flex-grow: 1;
    color: rgb(255, 255, 255);

}

.container .right .search-part i{
    font-size: 1.2rem;
    color:rgb(30, 50, 15);
    border:1.5rem solid rgb(158, 166, 112);
    background-color: rgb(158, 166, 112);
    margin: 0 0 0 2rem;
    
}


.container .right .weather-details{
    margin:2rem 0 0 0;
    padding:0 2rem 0 2rem;

}

.container .right .weather-details .details
{
    margin:4rem 0 0 0;

}

.container .right .weather-details .details .values{
    
    display: flex;
    justify-content: space-between;
    margin:1.5rem 0 0 0;
}


.container .right .weather-details .details .values span{
    color:rgb(172, 182, 115);
}